<template>
  <Transition
    name="fade-bounce"
    mode="out-in"
  >
    <MintModalPending v-if="pending" />
    <div
      v-else
      class="mint-modal-main"
    >
      <div class="mint-modal-main__content-mobile">
        <div class="mint-modal-main__numbers">
          <MintModalNumber
            class="mint-modal-main__number"
            label="supply"
            :price="MINT_TOTAL_SUPPLY - supply"
          />
          <MintModalNumber
            class="mint-modal-main__number"
            label="price"
            :price="price"
            has-icon
          />
        </div>
        <MintModalMainCounter
          v-model="amount"
          class="mint-modal-main__counter"
          :max="eligibleData.amount"
        />
      </div>
      <div class="mint-modal-main__info">
        <MintModalTokens class="mint-modal-main__info-tokens" />
        <MintModalPrice
          class="mint-modal-main__mint-button"
          :price="price * amount"
          @mint="signAndMint"
        />
      </div>
    </div>
  </Transition>
</template>

<script setup lang="ts">
import { ref, watch, } from 'vue';
import {
  useAccount,
  useContractWrite, useNetwork,
  useSignMessage,
  useWaitForTransaction
} from 'use-wagmi';
import {utils} from 'ethers';
import {createPublicClient, http} from 'viem';
import {goerli} from 'viem/chains';
import VastegaHarpisABI from '@/app/abi/VastegaHarips';
import { useHttpClient } from '@/shared/api';
import { uuidv4 } from '@/shared/lib/uuid';
import MintModalPrice from './mint-modal-main-price.vue';
import MintModalTokens from './mint-modal-main-tokens.vue';
import MintModalNumber from './mint-modal-main-number.vue';
import MintModalMainCounter from './mint-modal-main-counter.vue';
import {MintModalPending} from '../mint-modal-pending';
import { MINT_PRICE, MINT_TOTAL_SUPPLY } from '../mint-config';

interface Props {
  supply: number;
  eligibleData: {
    amount: number;
  }
}

interface Emits {
  (e: 'success'): void;
  (e: 'fail'): void;
  (e: 'no-money'): void;
  (e: 'wrong-network'): void;
}

defineProps<Props>();

const emits = defineEmits<Emits>();

const httpClient = useHttpClient();

const { address } = useAccount();

const { data: signMessageData, signMessage } = useSignMessage();

const price = ref(MINT_PRICE);
const signature = ref('');
const amount = ref(1);
const message = [`By signing this message you will`,
  'Nonce: ' + uuidv4()].join('\n');
const pending = ref(false);

const publicClient = createPublicClient({
  chain: goerli,
  transport: http(),
});

async function checkWalletBalance() {
  const balance = await publicClient.getBalance({
    address: address.value,
  });

  const money = utils.formatEther(balance);

  if (+money <= price.value * amount.value) {
    emits('no-money');

    return false;
  }

  return true;
}

const { chain, chains } = useNetwork();

async function checkNetwork() {
  const isCorrect = !!chains.value.find((item) => item.id === chain?.value.id);

  if (!isCorrect) {
    emits('wrong-network');
  }

  return isCorrect;
}

const signAndMint = async (): Promise<void> => {
  if ((await checkWalletBalance()) && (await checkNetwork)) {
    signMessage({ message });
  }
};

watch(signMessageData, async (value) => {
  if (value) {
    try {
      const data = await httpClient('/presale_auth', {
        method: 'POST',
        body: {
          signer: address.value,
          message,
          signature: value,
          amount: amount.value,
        }
      });

      signature.value = data.signature;

      mint();
    } catch (e) {
      console.log(e);
    }
  }
});

const { writeAsync: mintAsync } = useContractWrite({
  address: '0x1cB5E5C082AF921A128980BFbD0c3AF0076b5aE0',
  abi: VastegaHarpisABI,
  functionName: 'mint',
});

const hash = ref(undefined);

useWaitForTransaction({
  hash,
  onSuccess(data) {
    const iface = new utils.Interface(VastegaHarpisABI);
    const parsed = iface.parseLog(data.logs[0]);
    localStorage.setItem('minted-item-id', parsed.args.id);
    emits('success');
  },
  onError() {
    emits('fail');
  },
});

const mint = async (): Promise<void> => {
  try {
    const data = await mintAsync({
      args: [
        address.value,
        amount.value,
        price.value,
        signature.value,
      ]
    });

    hash.value = data.hash;
    localStorage.setItem('transaction-hash', data.hash);
    pending.value = true;
  } catch (e) {
    console.log(e);
    emits('fail');
  }
};
</script>

