<template>
  <div class="vastega-app">
    <Transition name="fade-bounce">
      <HomeMenu
        v-if="!hideInterface"
        class="vastega-app__menu"
        @mint="showMintModal = true"
        @vision="scrollToVision"
      />
    </Transition>

    <div
      v-if="isLoading"
      class="vastega-app__loader"
    />

    <main>
      <div class="vastega-app__zoomed">
        <HeroHome/>
        <HomePrologue/>
        <HomeClans
          @mint="showMintModal = true"
        />
        <div class="vastega-app__mint-button-wrapper">
          <BaseButton
            appearance="secondary"
            @click="showMintModal = true"
          >
            Mint now
          </BaseButton>
        </div>
      </div>
      <HomeVision
        @enter="hideInterface = true"
        @leave="hideInterface = false"
      />
      <div class="vastega-app__zoomed">
        <HomeRoadMap v-if="showSections"/>
        <HomeMore v-if="showSections"/>
        <HomeFooter v-if="showSections"/>
      </div>
    </main>
    <aside class="vastega-app__aside">
      <Transition name="fade-bounce">
        <HomeSlider
          v-if="!hideInterface"
          class="vastega-app__slider"
          @mint="showMintModal = true"
        />
      </Transition>
      <Transition name="fade-bounce">
        <HomePlayer v-if="!hideInterface" />
      </Transition>
    </aside>

    <MintModal v-model="showMintModal"/>
  </div>
</template>

<script setup lang="ts">
import Rellax from 'rellax'
import {onMounted, ref} from 'vue'

import useHandlers from '@/composable/useHandlers'

import {HeroHome} from '@/widgets/home-hero'
import {HomeClans} from '@/widgets/home-clans'
import {HomeMenu} from '@/widgets/home-menu'
import {HomePrologue} from '@/widgets/home-prologue'
import {HomeRoadMap} from '@/widgets/home-road-map'
import {HomeFooter} from '@/widgets/home-footer'
import {HomeMore} from '@/widgets/home-more'
import {MintModal} from '@/widgets/mint-modal';
import {HomeVision} from '@/widgets/home-vision';

import {HomePlayer} from '@/widgets/home-player'
import {HomeSlider} from '@/widgets/home-slider'
import { BaseButton } from '@/shared/ui/base-button';
import { scrollIntoViewElement } from '@/shared/lib/scroll-into-view';

const isLoading = ref(true)
const showSections = ref(false)
const showMintModal = ref(false);
const hideInterface = ref(false);

window.onbeforeunload = function () {
  window.scrollTo(0, 0)
}
onMounted(() => {
  document.onreadystatechange = () => {
    if (document.readyState === 'complete') {
      requestAnimationFrame(() => {
        requestAnimationFrame(() => {
          requestAnimationFrame(() => {
            isLoading.value = false
            new Rellax('.rellax')
            useHandlers()
          })
        })
      })
    }
  }

  window.addEventListener('scroll', () => {
    const MIN_SCROLL = 100;

    if (window.scrollY > MIN_SCROLL)
      showSections.value = true
  })
})

const scrollToVision = (): void => {
  const vision = document.querySelector('.home-vision');

  scrollIntoViewElement(vision, { y: 5 });
}
</script>

<style lang="scss">
