  position: relative;
  overflow: hidden;
  height: var(--height);

  @media screen and (max-width: 650px) {
    height: auto;
  }

  &__mint-button-wrapper {
    display: none;
    flex-direction: column;
    zoom: 400%;
    padding: 0 16px;
    margin-bottom: 60px;
    @media screen and (max-width: 650px) {
      display: flex;
    }
  }

  &__loader {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: var(--white);
    z-index: 999;
  }

  &__zoomed {
    position: relative;
    width: 1728px;
    height: var(--mainHeight);
    transform-origin: top left;
    will-change: transform;
    @media screen and (min-width: 651px) {
      transform: scale(calc(var(--width) / 1728));
    }
    @media screen and (max-width: 650px) {
      zoom: var(--zoom);
    }
  }

  &__menu {
    // @include transform;

    position: fixed;
    top: 30px;
    right: 30px;
    z-index: 10;
    display: flex;
    justify-content: flex-end;
    transform-origin: right top;
    @media screen and (min-width: 651px) {
      transform: scale(calc(var(--width) / 1728));
    }
  }

  @media (max-width: 650px) {
    .vastega-app__menu {
      display: block;
      top: 20px;
      right: 7px;
    }
  }

  &__parallax {
    position: relative;
  }

  &__aside {
    // @include transform;

    position: fixed;
    bottom: 16px;
    right: 17px;
    z-index: 10;
    display: flex;
    flex-direction: column;
    transform-origin: right bottom;
    @media screen and (min-width: 651px) {
      transform: scale(calc(var(--width) / 1728));
    }

    @media screen and (max-width: 650px) {
      zoom: var(--zoom);
      transform-origin: initial;
      width: 100%;
      right: 0;
      height: 400px;
      bottom: 0;
      flex-direction: row-reverse;
      align-items: end;
      justify-content: space-between;
      padding: 0 70px 80px;
    }
  }

  &__slider {
    margin-bottom: 9px;
    @media screen and (max-width: 650px) {
      zoom: 400%;
      margin-bottom: 0;
    }
  }
}
</style>
