<script lang="ts" setup>
import {ref, onMounted, onUnmounted, nextTick} from 'vue';
import {gsap} from 'gsap';
// eslint-disable-next-line import/no-named-as-default
import ScrollTrigger from 'gsap/ScrollTrigger';
import throttle from 'lodash/throttle';
import {BaseButton} from '@/shared/ui/base-button';
import {createId} from '@/shared/lib/create-id';
import {scrollIntoViewElement} from '@/shared/lib/scroll-into-view';
import HomeVisionLogo from './home-viison-logo.vue';

interface Emits {
  (e: 'enter'): void,
  (e: 'leave'): void,
}

const emits = defineEmits<Emits>();

const root = ref<HTMLImageElement | null>(null);
const sceneWrapper = ref<HTMLDivElement | null>(null);

const isReplaying = ref(false);

const showMenu = ref(false);

let firstTriggerId = createId('home-vision');

const createFirstTrigger = (): void => {
  firstTriggerId = createId('home-vision');

  ScrollTrigger.create({
    id: firstTriggerId,
    animation: gsap.timeline(),
    trigger: sceneWrapper.value,
    start: `0 0`,
    end: '8000px 100%',
    pin: true,
    pinSpacing: false,
    scrub: true,
    onEnter: () => {
      emits('enter');
      showMenu.value = true;
    },
    onLeaveBack: () => {
      emits('leave');
      showMenu.value = false;
      clearBombs();
    },
    onLeave() {
      emits('leave');
      showMenu.value = false;
      clearBombs();
    },
    onEnterBack() {
      emits('enter');
      showMenu.value = true;
    }
  });
};

const screenNumber = ref(0);
const prevScreenNumber = ref(0);

window.addEventListener('scroll', async () => {
  const controller = ScrollTrigger.getById(firstTriggerId);

  if (!controller) {
    return;
  }

  if (controller.progress < 0.01) {
    prevScreenNumber.value = screenNumber.value;
    screenNumber.value = -1;

    return;
  }

  const screen = Math.floor(5 * controller.progress);

  if (isReplaying.value && screen === 0) {
    return;
  }

  if (screen !== screenNumber.value && screen < 5) {
    clearBombs();
    requestAnimationFrame(() => {
      prevScreenNumber.value = screenNumber.value;
      screenNumber.value = screen;
    });
  }
});


const buildScene = (): void => {
  createFirstTrigger();
};

const destroyScene = (): void => {
  ScrollTrigger.getById(firstTriggerId)?.kill();
};

const offset = ref(0);

const setOffset = (): void => {
  const elementsTop = document.querySelector('.vastega-app__zoomed');
  const computedStyle = getComputedStyle(document.body);
  const zoom = parseFloat(computedStyle.getPropertyValue('--zoom'), 10) / 100;

  offset.value = elementsTop.offsetHeight * zoom - elementsTop.offsetHeight;
}

const rebuildScene = async (): Promise<void> => {
  destroyScene();
  buildScene();
  setOffset();
};

const throttledRebuildScene = throttle(rebuildScene, 500);

const refreshScene = (): void => {
  ScrollTrigger.getById(firstTriggerId)?.refresh();
  setOffset();
};

const throttledRefreshScene = throttle(refreshScene, 500);

let resizeObserver: ResizeObserver;

onMounted(async () => {
  gsap.registerPlugin(ScrollTrigger);

  window.addEventListener('resize', throttledRebuildScene);
  resizeObserver = new ResizeObserver(throttledRefreshScene);
  resizeObserver.observe(document.body);

  buildScene();
});

onUnmounted(() => {
  window.removeEventListener('resize', throttledRebuildScene);
  resizeObserver?.disconnect();

  destroyScene();
});

const replay = (): void => {
  isReplaying.value = true;
  scrollToSection(0);

  const gradients = document.querySelectorAll('.home-vision__gradient');

  [...gradients].forEach((item) => {
    item.addEventListener('animationend', () => {
      if (item.classList.contains('home-vision__gradient--replay')) {
        item.classList.remove('home-vision__gradient--replay');
        isReplaying.value = false;
      }
    }, { once: true })
  })
}

const scrollToSection = (screen: number): void => {
  const controller = ScrollTrigger.getById(firstTriggerId);

  const sceenSize = ((controller.end - controller.start) / 5);

  scrollIntoViewElement(root.value, {y: sceenSize * screen + 10, behavior: 'instant'});
}

function clearBombs() {
  const gradients = document.querySelectorAll('.home-vision__gradient--bomb');

  [...gradients].forEach((item) => {
    item.classList.remove('home-vision__gradient--bomb');
  });
}
</script>

