import { scrollToPromise } from './scroll-to-promise';

export const scrollIntoViewElement = (
  element: HTMLElement,
  _options: ScrollOptions & { y: number, scrollElement?: HTMLElement, },
  overflowElement: Window | Element = window,
): Promise<void> => {
  const {
    y = 0,
    behavior = 'smooth',
    scrollElement,
  } = _options;

  const computedStyle = getComputedStyle(document.documentElement);

  const top = overflowElement instanceof Window
    ? element.getBoundingClientRect().top
    : element.getBoundingClientRect().top - overflowElement.getBoundingClientRect().top;

  const options = {
    top: top + (overflowElement instanceof Window
      ? overflowElement.scrollY
      : overflowElement.scrollTop) + y,
    behavior,
  };

  return scrollToPromise(scrollElement || window, options);
};
