<template>
  <div class="home-vision-logo">
    <img
      v-show="!isReady"
      width="223px"
      height="245px"
      :src="`/animations/logo/${images[0]}.png`"
      alt="Capsule"
    >
    <canvas
      v-show="isReady"
      id="logo-canvas"
      class="home-vision-logo__canvas"
    />
  </div>
</template>

<script setup lang="ts">
import {onMounted, onUnmounted, ref} from 'vue';

const images = [...Array(120)].map((item, index) => 'S' + (index / 100).toFixed(2).replace('.', ''));

let timeout: typeof setTimeout = null;

const isReady = ref(false);

onMounted(async () => {
  const canvas = <HTMLCanvasElement>document.querySelector('#logo-canvas');
  const ctx = canvas.getContext('2d');

  canvas.width = 223;
  canvas.height = 245;

  ctx.scale(0.5, 0.5)

  const imageItems = await Promise.all(images.map(loadImage));
  let currentIndex = 0;

  isReady.value = true;

  const change = () => {
    if (currentIndex < images.length - 1) {
      currentIndex += 1;
    } else {
      currentIndex = 0;
    }
  }

  function animate() {
    const fps = 30;

    ctx.clearRect(0, 0, 10000, 10000)
    ctx.drawImage(imageItems[currentIndex], 0, 0);
    change();

    timeout = setTimeout(() => {
      requestAnimationFrame(animate);
    }, 1000 / fps);
  }

  animate();
});

onUnmounted(() => {
  if (timeout) {
    clearTimeout(timeout);
  }
});

function loadImage(image) {
  return new Promise((resolve) => {
      const img = new Image();

      img.src = `/animations/logo/${image}.png`;

      img.onload = function () {
        resolve(img);
      };
    }
  );
}
</script>

<style lang="scss">
#capsule-canvas {
  width: 100%;
}

.home-vision-logo {
  &__canvas {
    width: 100%;
  }
}
</style>
