import {createConfig, configureChains,} from 'use-wagmi';
import {mainnet, goerli} from 'use-wagmi/chains';
import {publicProvider} from 'use-wagmi/providers/public'
import {InjectedConnector} from 'use-wagmi/connectors/injected'
import {WalletConnectConnector} from 'use-wagmi/connectors/walletConnect'

const {chains, publicClient} = configureChains([goerli], [publicProvider()])

export const config = createConfig({
  // autoConnect: true,
  connectors: [
    new InjectedConnector({
      chains,
    }),
    new WalletConnectConnector({
      chains,
      options: {
        projectId: '88de3782c114d0fd00d0015f897a0f7d',
        showQrModal: true,
      },
    }),
  ],
  publicClient
})

export {
  publicClient
};
